.darkmode .slider-v2 {
  position: relative;
  display: block;
  width: 100%;
  /* height: 1em; */
  cursor: pointer;
  border-radius: 1.5em;
  transition: 350ms;
  background: linear-gradient(rgba(0, 0, 0, 0.07), rgba(255, 255, 255, 0)), #ddd;
  box-shadow: 0 0.07em 0.1em -0.1em rgba(0, 0, 0, 0.4) inset, 0 0.05em 0.08em -0.01em rgba(255, 255, 255, 0.7);
}

.darkmode .slider-v2::after {
  position: absolute;
  content: '';
  width: 0.5em;
  height: 0.5em;
  top: 0.2em;
  left: 0.8em;
  border-radius: 50%;
  transition: 250ms ease-in-out;
  background: linear-gradient(#f5f5f5 10%, #eee);
  box-shadow: 0 0.1em 0.15em -0.05em rgba(255, 255, 255, 0.9) inset, 0 0.2em 0.2em -0.12em rgba(0, 0, 0, 0.5);
}

.darkmode .slider-v2::before {
  position: absolute;
  content: '';
  width: 98%;
  height: 0.5em;
  top: 0.25em;
  left: 0.75em;
  border-radius: 0.75em;
  transition: 250ms ease-in-out;
  background: linear-gradient(rgba(0, 0, 0, 0.07), rgba(255, 255, 255, 0.1)), #d0d0d0;
  box-shadow: 0 0.08em 0.15em -0.1em rgba(0, 0, 0, 0.5) inset, 0 0.05em 0.08em -0.01em rgba(255, 255, 255, 0.7), 0 0 0 0 rgba(251, 76, 71, 0.7) inset;
}

.darkmode__checkbox {
  display: none;
}

.darkmode__checkbox:checked + .slider-v2::before {
  box-shadow: 0 0.08em 0.5em -0.1em rgba(0, 0, 0, 0.5) inset, 0 0.05em 0.08em -0.01em rgba(255, 255, 255, 0.7), 64em 0 0 0 rgba(251, 76, 71, 0.7) inset;
}

.darkmode__checkbox:checked + .slider-v2::after {
  left: 98.5%;
}

.dark-div {
  background: linear-gradient(90deg, #3758f5 31%, #220163 76%);
}

.dark-body {
  background: linear-gradient(0deg, #a19d9d 2%, black 100%);
}

.dark-common-button {
  background: #52057b;
}

.dark-stats-table {
  background: #939394 !important;
}

.dark-paragraph {
  color: #892cdc !important;
}
