@use 'App.scss';
:root {
  --font-size: 18px;
  --line-height: 22px;
  --font-style: normal;
  --margin-bottom-992px: 20px;
}

.header {
  margin-top: 31px;
  margin-bottom: 51px;
}

.header .logo__img {
  max-width: 200px;
}

.header .navlink {
  display: flex;
  justify-content: flex-end;
}

.header .navlink__link {
  color: var(--subTitle-color);
  font-family: var(--font-family);
  font-style: normal;
  font-style: var(--font-style);
  font-weight: normal;
  font-size: 18px;
  font-size: var(--font-size);
  line-height: 22px;
  line-height: var(--line-height);
  margin-left: 15px;
  text-decoration: none;
}

.header .navlink__link:hover {
  color: var(--selection-color);
}

.header .navlink .active {
  position: relative;
  color: var(--selection-color);
}

.header .navlink .active::after {
  content: "";
  position: absolute;
  background-color: var(--selection-color);
  top: 28px;
  left: 50%;
  transform: translateX(-50%);
  height: 8px;
  width: 8px;
  border-radius: 100%;
}

.header .contact__btn {
  background: transparent;
  border: 2px solid var(--selection-color);
  border-radius: 46px;
  color: var(--selection-color);
  padding: 16px 45px;
  font-family: var(--font-family);
  font-style: normal;
  font-style: var(--font-style);
  font-weight: 500;
  font-size: 18px;
  font-size: var(--font-size);
  line-height: 22px;
  line-height: var(--line-height);
}

.header .contact__btn:hover {
  color: white;
  background-color: var(--selection-color);
}

@media screen and (max-width: 991px) {
  .header .logo {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    margin-bottom: var(--margin-bottom-992px);
  }
  .header .navlink {
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    margin-bottom: 20px;
    margin-bottom: var(--margin-bottom-992px);
  }
  .header .active {
    text-decoration: underline;
  }
  .header .active::after {
    display: none;
  }
  .header .contact {
    display: flex;
    justify-content: center;
  }
}
